*{margin:0;padding:0;box-sizing:border-box}#container{display:grid;justify-items:center;height:100vh}

#todo-edit{margin-top:10px;width:30vw;padding:15px 5px;font-family:"Original Surfer",cursive;font-size:1.5rem}#add-todo{position:relative;bottom:3px;padding:17px 15px;margin-left:8px;font-size:1rem;background-color:#fff;color:#50a150;border:2px solid #50a150;border-radius:10px;cursor:pointer;transition:all 0.2s linear}#add-todo:hover{background-color:#50a150;color:#fff}

.active-task-onEdit-input{border:none;padding:5px;font-family:"Original Surfer",cursive;font-size:1.5rem;width:79%;background-color:#548235;border-radius:10px}.active-task-onView-input{border:none;padding:5px;font-family:"Original Surfer",cursive;font-size:1.5rem;width:79%;background-color:#385723;color:#fff}.finished-task{border:none;padding:5px;font-family:"Original Surfer",cursive;font-size:1.5rem;width:79%;background-color:#767171;color:#000;text-decoration:line-through}

.active-status-btn{background-color:#70ad47;border:none;border-radius:50%;width:20px;height:20px;cursor:pointer;position:relative;bottom:0.5vh}.finished-status-btn{background-color:#bfbfbf;border:none;border-radius:50%;width:20px;height:20px;cursor:pointer;position:relative;bottom:0.5vh}.edit-btn{border:none;cursor:pointer;margin:0 5px;padding:15px;border-radius:10px;background-color:#f2f2f2}.delete-btn{border:none;cursor:pointer;margin:0 5px;padding:15px;border-radius:10px;background-color:#fcc}.save-btn{border:none;cursor:pointer;margin:0 5px;padding:15px;border-radius:10px;background-color:#d5e5f4}.undo-btn{border:none;cursor:pointer;margin:0 5px;padding:15px;border-radius:10px;background-color:#dad9d9}.edit-logo{color:#000}.delete-logo{color:#c00}.save-logo{color:#5b9bd5}.undo-logo{color:#767171}

#list{list-style:none;width:90%;display:grid}#list #list-item{padding:30px;margin:10px}#list .active-item{padding:10px;margin:10px;border-radius:10px;background-color:#385723}#list .finished-item{padding:10px;margin:10px;border-radius:10px;background-color:#767171}

#main-app{width:clamp(400px, 80vw, 720px)}#main-app #head{padding:50px}#main-app #head #app-title{font-family:"RocknRoll One",sans-serif;font-size:3rem}#main-app #todo-list{border-radius:10px;display:grid;justify-items:center}

